.sidebar {
    position: fixed;
    top: 0;
    right: 10px;
    width: 100px;
}

.sidebar .img-wrapper {
    width: 100%;
    height: 50px;
    margin-bottom: 10px;
    margin-top: 10px;
    cursor: pointer;
}

.sidebar .img-wrapper img {
    height: 100%;
    width: 100%;
}

